button:focus {
  outline:0;
}

.button-container {
  background: black;
  display: grid;
  align-self: center;
  height: 100vh;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 150px;
  color: white;
  border-right: solid 1px white;
  z-index: 2;
  grid-area: button;
}
.button {
  justify-content: space-between;
  background: none;
  background-color: #000;
  color: white;
  border: none;
  justify-self: center;
  display: inherit;
  text-decoration: none;
  font-family: 'Russo One', sans-serif;
}

.fab {
  font-size: 100px;
  -webkit-transition: all .25s ease-in-out;
  transition: all .25s ease-in-out;
}

.fab:hover {
  font-size: 120px;
}

.selected .fab {
  font-size: 120px;
  color: #FEDA4B;
}

@media screen and (max-width: 544px) {
  .button-container {
    display: inline-flex;
    grid-template-areas: 
    'button'
    'card-container';
    position: absolute;
    width: 100%;
    font-size: 0px;
    justify-content: space-around; 
  }
  .button {
    display: flex;
  }
  .fab {
    font-size: 80px;
  }
  .selected .fab {
    font-size: 80px;
  }
  .fab:hover {
    font-size: 90px;
  }
}

@media screen and (max-width: 400px) {
  .button-container {
    display: inline-flex;
    grid-template-areas: 
    'button'
    'header'
    'card-container';
    position: absolute;
    width: 100%;
    font-size: 0px;
    justify-content: space-around; 
  }
  .button {
    display: flex;
  }
  .fab {
    font-size: 40px;
  }
  .selected .fab {
    font-size: 40px;
  }
  .fab:hover {
    font-size: 50px;
  }
}

.scroll-container{
  grid-column: 2 / span 1;
}


.card-container {
background: transparent;
display: flex;
flex-direction: row;
flex-wrap: wrap;
grid-area: card-container;
grid-gap: 10px;
justify-content: space-around;
padding: 10px;
position: fixed;
z-index: 2;
height: 100%;
overflow-y: scroll;
margin-bottom: 30px;
}

.no-favs{
  color: #FEDA4B;
  font-size: 2em;
}
.card {
  border: 5px solid #FEDA4B;
  background-color: black;
  display: grid;
  border-radius: 20px;
  width: 225px;
  margin: 1em;
  height: 325px;
  color: #FEDA4B;
  justify-items: left;
  padding-left: 7px;
  margin-bottom: 6em;
  font-family: 'Russo One', sans-serif;
}

.favorited {
  color: #FEDA4B;
}

.mil-falc {
  background-color: black;
  height: 30px;
  display: block;
}

h3 {
  margin: 0;
}

button {
  background-color: black;
  border: none;
  color: white;
  display: inline-block;
}

.mil-falc:hover {
  height: 40px;
  -webkit-transition: all .25s ease-in-out;
  transition: all .25s ease-in-out;
  cursor: pointer;
}

